
.game_bg1{
  background: url(http://www.dke.cn/theme/game_bg1.png) no-repeat center center #e9eff3;
  height: 320px;
}
.game_tab1{
  padding-top: 60px;
  padding-bottom: 60px;
}
.game_box1{
  height: 150px;
  text-align: center;
}
.logo img{
  margin: 0 auto;
}
.game_box1 .photo{
  height: 90px;
  line-height: 90px;
}
.game_box1 .photo img{
  height: 90px;
  margin: 0 auto;
}
.game_box1 p{
  margin-top: 15px;
  font-size: 20px;
}
.game_bg2{
  background-color: #e9eff3;
  padding-top: 50px;
  padding-bottom: 50px;
}
.game_bg2 div.title{
  height: 58px;
  margin-bottom: 50px;
  background: url(http://www.dke.cn/images/game_promotion.png) no-repeat center center #e9eff3;
}
.game_bg2 p.info{
  color: #696969;
  margin-top: 40px;
}
.game_bg2 div.photo{
  height: 200px;
  background: url(http://www.dke.cn/images/game_bg3_1.png) no-repeat center center #e9eff3;
}
.game_tab2{
  padding-top: 50px;
  padding-bottom: 50px;
}
.game_tab2 div.title{
  height: 58px;
  margin-bottom: 50px;
  background: url(http://www.dke.cn/images/game_bg4_1.png) no-repeat center center #fff;
}
.game_tab2 p.title{
  color: #53B830;
  font-size: 22px;
  margin-top: 60px;
  text-align: center;
}
.game_tab2 p.info{
  color: #696969;
  font-size: 16px;
  margin-top: 30px;
}
.game_tab2 div.tab_photo1{
  height: 250px;
  background: url(http://www.dke.cn/images/game_bg4_2.png) no-repeat center center #fff;
}
.game_tab2 div.tab_photo2{
  height: 250px;
  background: url(http://www.dke.cn/images/game_bg4_3.png) no-repeat center center #fff;
}
.game_tab2 div.tab_photo3{
  height: 250px;
  background: url(http://www.dke.cn/images/game_bg4_4.png) no-repeat center center #fff;
}
.game_tab2 div.tab_photo4{
  height: 250px;
  background: url(http://www.dke.cn/images/game_bg4_5.png) no-repeat center center #fff;
}
.game_tab3{
  background-color: #e9eff3;
  padding-top: 50px;
  padding-bottom: 50px;
}
.game_tab3 div.title{
  height: 58px;
  margin-bottom: 50px;
  background: url(http://www.dke.cn/images/game_bg5_1.png) no-repeat center center #e9eff3;
}
.game_tab3 .img-responsive{
  margin: 10px auto;
}
.game_bg3{
  height: 447px;
  text-align: center;
  background: url(http://www.dke.cn/theme/game_bg6.jpg) no-repeat center center #e9eff3;
}
.game_bg3 button{
  color: #53B82F;
  font-size: 22px;
  background: transparent;
  border: solid 1px #53B82F;
  margin-top: 300px;
}
.game_bg3 button:hover,.game_bg3 button:focus{
  color: #53B82F;
}
@media (min-width: 992px){
.game_box1 .photo{
    height: 100px;
    line-height: 100px;
}
.game_box1 .photo img{
    height: 100px;
}
.game_box1 p{
    margin-top: 10px;
    font-size: 22px;
}
}
@media (min-width: 768px){
.game_tab2 p.title{
    text-align: left;
}
}



